

.brend_home{
    display: flex;
    margin-top: 50px;
    flex-wrap: wrap;
    justify-content: center;
    gap: 40px;
}

.brend__title{
    display: flex;
    align-items: center;
    width: 416px;
    height: 572px;
    flex-direction: column;
    gap: 80px;
}

.brend__title h3{
    font-family: Roboto,sans-serif;
    font-size: 28px;
    font-weight: 500;
    line-height: 33px;
    letter-spacing: 0em;
    text-align: left;
}

.brend__title .sum {
    font-family: Roboto,sans-serif;
    font-size: 24px;
    font-weight: 500;
    line-height: 28px;
    letter-spacing: 0em;
    text-align: left;
    padding-top: 50px;
}

.brend__title .light{
    font-family: Roboto,sans-serif;
    font-size: 18px;
    font-weight: 300;
    line-height: 21px;
    letter-spacing: 0em;
    text-align: left;
    padding-top: 29px;
}

.brend__lacation{
    display: flex;
    gap: 5px;
    margin-top: 37px;
}

.knopka{
    margin-top: 16px;
}

.brend__select{
    margin-top: 34px;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    flex-direction: column;
    gap: 16px;
}

.brend__select select{
    width: 367px;
    height: 54px;
    border: 1px;
    color:#323334;
    border: 1px solid #323334;
}

.brend__select button{
    width: 367px;
    height: 54px;
    background: #4C8787;
    color: white;
    box-shadow: 0px 4px 4px 0px #00000040;
}
.brend_fot{
    margin-top: 30px;
    display: flex;
    margin-left: 80px;
    flex-direction: column;
    gap: 30px;
}

.img__brend img{
    width: 848px;
    height: 586px;
}

.bg__brand{
    background: url(../../public/assets/img/bgoronge.png);
    width: 856px;
    height: 309px;
    opacity: 0.7px;
}

.brend_bg{
    padding: 57px 78px 57px 78px;
}

.bg__brand h6{
    font-size: 18px;
}

.brend_bg .duvet{
    padding-top: 25px;
    font-family: Roboto;
    font-size: 18px;
    font-style: italic;
    font-weight: 300;
    line-height: 21px;
    letter-spacing: 0.03em;
    text-align: left;
}
.brend_bg .cottom{
    padding-top: 25px;
    font-family: Roboto;
    font-size: 18px;
    font-weight: 400;
    line-height: 21px;
    letter-spacing: 0.03em;
    text-align: left;
}

.art{
    padding-top: 20px;
    padding-left: 70px;
}

.brendrom__slider{
    margin-top: 50px;
}