

.skidka{
    background: url(../../public/assets/img/bggg.png);
    width: 100%;
    height: 265px;   
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 122px;
}

.skidka__text{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content:center ;
    flex-direction: column;
}

.skidka h1{
    font-family: Roboto;
    font-size: 38px;
    font-weight: 500;
    line-height: 45px;
    letter-spacing: 0em;
    text-align: left;
    
    font-family: Roboto;
    font-size: 38px;
    font-weight: 500;
    line-height: 45px;
    letter-spacing: 0em;
    text-align: left;
    
    font-family: Roboto;
    font-size: 38px;
    font-weight: 700;
    line-height: 45px;
    letter-spacing: 0em;
    text-align: left;    
}

.skidka .off{
    font-family: Roboto;
    font-size: 38px;
    font-weight: 400;
    line-height: 45px;
    letter-spacing: 0em;
    text-align: left;
    padding-top: 15px;    
}

.skidka .not{
    padding-top: 56px;
    font-family: Roboto,sans-serif;
    font-size: 12px;
    font-weight: 500;
    line-height: 21px;
    letter-spacing: 0em;
    text-align: left;
}

@media(max-width:975px) and (min-width:587px){
    .skidka h1{
        font-family: Roboto;
        font-size: 20px;
        font-weight: 500;
        line-height: 45px;
        letter-spacing: 0em;
        text-align: left;
      
    }
    
    .skidka .off{
        font-family: Roboto;
        font-size: 20px;
        font-weight: 400;
        line-height: 45px;
        letter-spacing: 0em;
        text-align: left;
        padding-top: 15px;    
    }
    
    .skidka .not{
        padding-top: 56px;
        font-family: Roboto,sans-serif;
        font-size: 10px;
        font-weight: 500;
        line-height: 21px;
        letter-spacing: 0em;
        text-align: left;
    }
}

@media(max-width:586px) and (min-width:200px){
    .skidka{
        background: url(../../public/assets/img/bggg.png);
        width: 100%;
        height: 265px;   
        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 122px;
    }
    
    .skidka__text{
        display: flex;
        align-items: center;
        text-align: center;
        justify-content:center ;
        flex-direction: column;
        display: block;
    }
    
    .skidka h1{
        font-family: Roboto;
        font-size: 28px;
        font-weight: 500;
        line-height: 45px;
        letter-spacing: 0em;
        text-align: left;   
    }
    
    .skidka .off{
        font-family: Roboto;
        font-size: 18px;
        font-weight: 400;
        line-height: 45px;
        letter-spacing: 0em;
        text-align: left;
        padding-top: 15px;    
        display: block;
    }
    
    .skidka .not{
        padding-top: 56px;
        font-family: Roboto,sans-serif;
        font-size: 12px;
        font-weight: 500;
        line-height: 21px;
        letter-spacing: 0em;
        text-align: left;
        display: block;
    }
}

