
*{
    text-decoration: none;
    list-style: none;
    
}


footer{
    width: 100%;
    background: #0A032A;
    margin-top: 110px;
}


.footer__dena{
    display: flex;
    align-items: center;
    gap: 130px;
    justify-content: center;
    flex-wrap: wrap;
}

.footer__dena h1{
    font-family: Roboto;
    font-size: 28px;
    font-weight: 700;
    line-height: 33px;
    letter-spacing: 0em;
    text-align: left;  
    padding-top: 100px;  
    color: #F3F6FF;
}

.footer__dena p{
    font-family: Roboto;
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0em;
    text-align: left;
    color: #FFFFFF80;
    padding-top: 41px;
}

.footer__dena h2{
    font-family: Roboto;
    font-size: 28px;
    font-weight: 700;
    line-height: 33px;
    letter-spacing: 0em;
    text-align: left;  
    padding-top: 100px;  
    color: #F3F6FF;
}

.footer__dena h3{
    color: #F3F6FF;
}
.footer__dena ul{
    display: flex;
    flex-direction: column;
    gap: 16px;
    padding-top: 41px;
}

.footer__dena ul li{
    font-family: Roboto;
    font-size: 14px;
    font-weight: 300;
    line-height: 16px;
    letter-spacing: 0em;
    text-align: left;
    color: #FFFFFF80;
}

.fw ul li{
    color: #F3F6FF;
}

.footer__inst{
    display: flex;
    align-items: center;
    gap: 50px;
    padding-top: 55px;
}

.line{
    width: 296px;
    border: 1px;
    border: 1px solid #FFFFFF80;

}

.footer__footer{
    text-align: center;
    background:#050019;
    height: 52px;
    align-items: center;
    justify-content: center;
}

.footer__footer h3{
    font-family: Roboto;
    font-size: 12px;
    font-weight: 300;
    line-height: 14px;
    letter-spacing: 0em;
    color: #FFFFFF;
    padding-top: 20px;
}


@media (max-width:587px) and (min-width:270px){

    .footer__dena{
        display: flex;
        align-items: center;
        gap: 0px;
        text-align: center;
        justify-content: center;
        flex-direction: column;
    }

    .fw h1{
        padding-top: 20px;
    }
    .fw ul {
        padding-top: 50px;
    }

    .fw ul li{
        font-family: Roboto;
        font-size: 16px;
        font-weight: 700;
        line-height: 19px;
        letter-spacing: 0em;
        text-align: center;
        color:#FFFFFF;        
    }

    .ft ul{
        padding-top: 64px;
    }
    .ft ul li{
        font-family: Roboto;
        font-size: 16px;
        font-weight: 700;
        line-height: 19px;
        letter-spacing: 0em;
        text-align: center;
        color:#FFFFFF;        
    }

    .fw_king{
        display: none;
    }
    .fw_tel{
        display: flex;
        align-items: center;
        text-align: center;
        justify-content: center;
    }

    .ft h3{
        display: none;
    }

    .fw .footer__inst, p{
        display: none;
    }



    .fs{
        display: none;
    }

    .ff{
        display: none;
    }
}

@media (max-width:1920px) and (min-width:588px){
    .fw_tel{
        display: none;
    }
}