
.mini__carusel{
    margin-top: 20px;
}

.mini__carusel img{
    width: 200px;
    height: 204px;
}

.mini__carusel.slick-slider{
    display: flex;
    align-items: center;
    justify-content: center;
}
.mini__carusel .slick-slider .slick-arrow{
    top: 200;
    right: 35px;
    background: transparent;
}

.slick-slider .slick-prev{
    top: 100;
    right: 100px;
    z-index: 99;
}

@media(max-width:1920px) and (min-width:757px){
    .mini__carusel{
        display: none;
    }
}