
.korzinka__korzinka{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 50px;
}

.korzinka__left_left{
    display: flex;
    align-items: center;
}


.korzinka__left_left button img{
    width: 35px;
    height: 30px;
}

.korzinka__left_left button{
    background: transparent;
    outline: 0;
    border: 0;
    margin-top: 40px;
}

.korzinka__left_left p{
    padding-top: 17px;
}

.korzinka__right{
    display: flex;
    align-items: center;
    flex-direction: column;
}

.korzinka__right{
    width: 510px;
    height: 312px;
    background: #ECEBE9B2;
}

 .form__corn h5{
    font-family: Roboto,sans-serif;
    font-size: 18px;
    font-weight: 300;
    line-height: 21px;
    letter-spacing: 0em;
    text-align: left;
    color: #323334;
    margin-top: 25px;
}

.form__corn form input{
    width: 410px;
    height: 54px;
    margin-top: 20px;
    padding-left: 20px;
}

.form__corn form button{
    width: 65px;
    height: 54px;
    box-shadow: 0px 4px 4px 0px #00000040;
    background: #4C8787;
    color: white;
}
.right_price p{
    font-family: Roboto;
    font-size: 14px;
    font-weight: 500;
    line-height: 16px;
    letter-spacing: 0em;
    text-align: left;
    padding-top: 60px;
}

.right_price{
    display: flex;
    align-items: center;
    justify-content: center;
    justify-content: space-between;
}

.karzinka__button{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
}
.karzinka__button button{
    width: 416px;
    height: 54px;
    background: #4C8787;
    color: white;    
}

.korzinka__slider{
    margin-top: 30px;
}

.korzinka__slider.slick-slider .slick-arrow{
    background: black;
}

.korzinka__slider.slick-slider .slick-prev{
    background: black;
}
