/* taks css qowilgan */

.cards{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 57px;
    gap: 10px;
    margin-bottom: 50px;
    
}

.card{
    background: url(../../../public/assets/img/sunduk.png);
        width: 20em;
        height: 25em;
        margin: 0 1em;
        background-size: cover;
        box-shadow: #000 0px 11px 15px 0px;
        position: relative;
       margin-top: 50px;
}

.card:nth-child(1){
    background: url(https://img.freepik.com/premium-photo/orange-gladiolus-flower-stem-white-ceramic-vase-with-shadows-isolated-white-background_433823-165.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    width: 306px;
    height: 566px;
    display: flex;
    align-items: center;
    padding-left: 50px;
    justify-content: center;
}

.card:nth-child(2){
    background: url(../../../public/assets/img/sochiq.png);
}

.card:nth-child(4){
    background: url(../../../public/assets/img/talin.png);
}
.card:nth-child(5){
    background: url(../../../public/assets/img/sunduk2.png);
}
.card:nth-child(6){
    background: url(../../../public/assets/img/lemon.png);
}
.card:nth-child(7){
    background: url(../../../public/assets/img/savat.png);
}


.card:nth-child(8){
    background: url(https://cdn.create.vista.com/api/media/small/381260148/stock-photo-red-alstroemeria-vase-wooden-surface-isolated-beige);
    background-repeat: no-repeat;
    background-size: cover;
    width: 306px;
    height: 566px;
    display: flex;
    align-items: center;
    padding-left: 50px;
    justify-content: center;
}

.card__text{
    position: absolute;
    z-index: 99;
    bottom: -70px;
}

.card h1{
    font-family: Roboto;
    font-size: 18px;
    font-weight: 700;
    line-height: 21px;
    letter-spacing: 0em;
    text-align: left;
}
.card__text img{
    margin-right: 90px;
}
.card__text p{
    padding-right: 150px;
}

.oq img{
    width: 45px;
}
.oq{
    display: flex;
    justify-content: center;
}
.oq .korab{
    position: absolute;
    left: 100px;
}

.oq button{
    position: absolute;
    width: 70px;
    background: transparent;
    right: -60px;
    top: 140px;
    border: 0;
    outline: 0;
}
.owerfley{
    display: flex;
    align-items: center;
}

.card .owerfley{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    flex-direction: column;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: 0.3s;
}

.card:nth-child(1){
    display: flex;
    width: 306px;
    height: 566px;
    align-items: center;
    justify-content: center;
    gap: 0px;
    text-align: center;
    border-radius: 18px;
     transition: 8s;
}

.card .blue{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    flex-direction: column;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: 0.3s;
}
.blue{
    left: 0;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
}

.blue h1{
    padding-right: 150px;
    font-family: Roboto,sans-serif;
    font-size: 28px;
    font-weight: 700;
    line-height: 33px;
    letter-spacing: 0em;
    text-align: left;
    
}
.blue p{
    padding-top: 20px;
    font-family: Roboto,sans-serif;
    font-size: 16px;
    font-weight: 700;
    line-height: 22px;
    letter-spacing: 0em;
    text-align: left;
}
.oq{
    display: flex;
    width: 232px;
    height: 56px;
    align-items: center;
    justify-content: center;
    gap: 20px;
    text-align: center;
    background: white;
    border-radius: 18px;
     transition: 8s;
}

.owerfley p{
    width: 2px;
    height: 29.5px;
    border: 1px;
    background: #00000080;

    
}

.card:hover .owerfley{opacity: 1;}
.card:hover .blue{opacity: 1;}

.card .btn:hover {
    background: rgba(255, 255, 255, 0.3);
}

.card .btn, .card:hover img{
    transition: 0.8s;
    transform: translateY(6px);
}

.card:hover .btn, .card:hover img{
    transform: translateY(0)
}

.card_text{
    position: absolute;
    bottom: 0;
}

