*{
    list-style: none;
    text-decoration: none;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.navbar__page{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: space-between;
    padding: 21px 15px 0 69px;
}

.navbar__page .logo{
    font-family: Roboto;
    font-size: 28px;
    font-weight: 700;
    line-height: 33px;
    letter-spacing: 0em;
    text-align: left;
    display: flex;
    align-items: center;
    gap: 12px;
    color: #323334;
}

.navbar__page .user{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 28px;
}

.user .btn{
    background: transparent;
    border: 0px solid transparent;
}

.user .bur{
    width: 50px;
    height: 45px;
}

.navbar__menu{
    display: flex;
    align-items: center;
    text-align: center;
    height: 50px;
    margin-top: 20px;
    border-top: 1px black solid;
    border-bottom: 1px black solid;
}
.navbar__menu ul{
    margin: 0 auto;
    text-align: center;
    display: flex;
    gap: 30px;
    /* resposive gap qb korw kere */
}
.navbar__menu ul li a{
    font-family: Roboto,sans-serif;
    font-size: 13px;
    font-weight: 700;
    line-height: 19px;
    letter-spacing: 0em;
    text-align: left;
    color:#323334;   
}

