 }

 .navbar__m{
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    
 }

.navbar__menu{
    width: 80%;
}

.logo{
    position: relative;
}

.burger__menu{
    position: absolute;
    top: 0;
    left: 0;
    width: 200px;
    height: 550px;
    border-radius: 5px; 
    background: #fff;
    box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.08);
    z-index: 99;
    background: white;
}

.cantact__menu{
    position: absolute;
    top: 20px;
    right: 180px;
    width: 200px;
    height: 200px;
    border-radius: 5px; 
    background: white;
    box-shadow: 0px 0px 16px 0px rgba(0, 0, 0, 0.08);
    z-index: 1;
}

@media(max-width:521px) and (min-width:433px){
