.navbar__form_cantrol{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
    gap: 8px;
}

.navbar__form_cantrol .navbar__form{
    position: relative;
}

.navbar__form img{
    position: absolute;
    left: 14px;
    top: 16px;
}

.navbar__form_cantrol input{
    width: 562px;
    height: 54px;
    border-radius: 25px;
    border: 2px;
    border: 2px solid #050019;
    padding-left: 20px;
}


.navbar__form_cantrol .btn{
    width: 146.27px;
    height: 63px;
    background: #4C8787;
    color: white; 
    font-family: Roboto,sans-serif;
    font-size: 18px;
    font-weight: 600;
    line-height: 0px;
    letter-spacing: 0.03em;
    text-align: center;
}



